
<template>
  <div id="loginPage">
    <img src="../../assets/images/bk.jpg" alt="" class="login-bk lazy-loadImg">
    <!-- <div class="titleName"></div>
    <div class="ValueName"></div> -->
    <div class="loginWindow">
      <div class="accountPassword">
        <div class="welcomeLogin">欢迎登录</div>
        <div class="account">
          <!-- <div class="accountText">账号</div> -->
          <el-input v-model="userLogin.username" class="accountNum" placeholder="请输入账号"></el-input>
        </div>
        <div class="password">
          <!-- @keyup.enter.native="" -->
          <!-- <div class="passwordText">密码</div> -->
          <el-input type="password" v-model="userLogin.password" class="passwordInput" placeholder="请输入密码" @keyup.enter.native="toHome"></el-input>
        </div>
        <!-- <div class="checkbox">
          <el-checkbox v-model="checked" class="checkedBox"
            >记住密码</el-checkbox
          >
        </div> -->
        <div class="loginIng">
          <div class="loginBtn" @click="toHome"><span>登 录</span></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import qs from "qs";
// import 'querystring'
export default {
  data() {
    return {
      checked: false,
      userLogin: {
        username: "",
        password: "",
      },
    };
  },
  created() {},
  mounted() {
    document.querySelector(".loginBtn").onmousemove = (e) => {
      const x =
        e.pageX - e.target.offsetLeft - (document.body.clientWidth - 505) / 2;
      const y =
        e.pageY - e.target.offsetTop - (document.body.clientHeight - 465) / 2;
      e.target.style.setProperty("--x", `${x}px`);
      e.target.style.setProperty("--y", `${y}px`);
    };
  },
  methods: {
    toHome() {
      this.$Lpost($url.DATA_URL + "/login", qs.stringify(this.userLogin), {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded;charset=utf-8",
        },
      }).then((res) => {
        if (res.data.code === 0) {
          this.cookie.setCookie({ username: this.userLogin.username }, 7);
          this.$router.push("/index");
          this.userLogin = {
            username: "",
            password: "",
          };
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    LoginHome(account, password) {
      this.userLogin = {
        username: account,
        password: password,
      };
      this.$Lpost($url.DATA_URL + "/login", qs.stringify(this.userLogin), {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded;charset=utf-8",
        },
      }).then((res) => {
        if (res.data.code === 0) {
          this.cookie.setCookie(this.userLogin, 7);
          this.$router.push("/home");
          console.log(this.cookie.getCookie("SESSION"));
          this.userLogin = {
            username: "",
            password: "",
          };
        }
      });
    },
  },
};
</script>
<style scoped lang="scss">
:root {
  --x: 0;
  --y: 0;
  --size: 0;
}
#loginPage {
  width: 100%;
  height: 100%;
  // background: url('../../assets/images/bk.jpg');
  // // background: url('../../assets/images/bk.gif');
  // background-size: 100% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  .login-bk {
    width: 100%;
    height: 100%;
    position: absolute;
  }
  .loginWindow {
    width: 505px;
    height: 465px;
    background: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    backdrop-filter: blur(10px);
    .accountPassword {
      width: 80%;
      height: 80%;
      .welcomeLogin {
        font-size: 30px;
        text-align: center;
        color: #8dc0eb;
        font-weight: 800;
        font-family: Sans-serif;
      }
      .account {
        margin-top: 40px;
        color: #8dc0eb;
        font-size: 18;
        .accountText {
          font-size: 16px;
        }
        .accountNum {
          margin-top: 5px;
          width: 100%;
          height: 40px;
          border-radius: 5px;
          font-size: 16px;
        }
      }
      .password {
        margin-top: 35px;
        color: #8dc0eb;
        font-size: 18;
        .passwordText {
          font-size: 16px;
        }
        .passwordInput {
          margin-top: 5px;
          width: 100%;
          height: 40px;
          border-radius: 5px;
          font-size: 14px;
        }
      }
      .checkbox {
        margin-top: 10px;
        color: #424b79;
        // font-size: 18px;
        .checkedBox {
          font-size: 16px;
        }
      }
      .loginBtn {
        // width: 100%;
        // height: 50px;
        background: #215390;
        font-size: 20px;
        color: #fff;
        font-weight: 800;
        line-height: 50px;
        text-align: center;
        border: none;
        border-radius: 5px;
        margin-top: 70px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        span {
          position: relative;
        }
        &::before {
          --size: 0;
          content: "";
          position: absolute;
          left: var(--x);
          top: var(--y);
          width: var(--size);
          height: var(--size);
          background: radial-gradient(
            circle closest-side,
            #4405f7,
            transparent
          );
          transform: translate(-50%, -50%);
          transition: width 0.2s ease, height 0.2s ease;
        }
        &:hover::before {
          --size: 400px;
        }
      }
    }
  }
}
.lazy-loadImg {
  animation: reveal 1s ease-out;
}
@keyframes reveal {
  0% {
    transform: scale(1.05);
    opacity: 0;
  }

  100% {
    transform: scale(1);
    opacity: 1;
  }
}
</style>
<style >
#loginPage .el-input__inner {
  background: #243f5c;
  border: 1px solid #354d65;
  color: #ddd;
}
#loginPage .el-input__inner::-webkit-input-placeholder {
  color: #7792af;
}
</style>